.input-textarea {
  border-bottom-width: 0;
  border-color: #ddd;
  border-left: none;
  border-right: none;
  border-top: none;
  color: rgba(0, 0, 0, 0.87);
  width: 100%;
  padding: 20px;
  min-height: 280px;
  font-size: 18px;
  line-height: 28px;
  resize: none;

  overflow: auto hidden;
  box-sizing: border-box;
  direction: ltr;
}

.input-textarea:focus {
  border-bottom: 0;
  outline: none !important;
}

.result-textarea {
  border-bottom-width: 0;
  border-color: #ddd;
  border-left: none;
  border-right: none;
  border-top: none;
  width: 100%;
  padding: 20px;
  font-size: 18px;
  line-height: 28px;
  min-height: 100px;
  resize: none;
  color: rgba(0, 0, 0, 0.87);
  background-color: #f5f5f5;
}

.result-textarea:focus {
  border-bottom: 0;
  outline: none !important;
}

.copy-button {
  float: right;
}
